import React, { Component } from 'react';
import './css/TodoList.css';
import calendarIcon from './../../icons/calendar.svg'
import TaskBox from './TaskBox'
import db from './../../db';
class TodoList extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tasks: []
        };
    }
    
    componentDidMount() {
        db.tasks
        .orderBy('date')
        .toArray()
        .then((tasks) => {
          this.setState({ tasks: tasks });
        });
        

    }
    render() {
        return (
            <div id="todolist">
                <div id="headerdiv" className="header">
                    <input type="text" id="TitleInput" placeholder="Title..." /> <span><input type=
                        "image" placeholder="Date..." id="datepicker" src={calendarIcon} readOnly />
                    </span>
                    <button className="addBtn">+</button>
                </div>
                {(this.state.tasks && this.state.tasks.length === 0) &&
                    <div className="noTasksBanner">
                        <p>Currently you don't have any tasks on your list</p>
                        <p>Add some tasks or <a href="#/setup">integrate your To-Do list with other
 services</a>
                        </p>
                    </div>
                }
                <ul id="tasksUL">
                    {this.state.tasks.map((task) => 
                        <TaskBox 
                        title={task.title}
                        date={task.date}
                        source={task.source}
                        list_id={task.tasklistid}
                        id={task.id}
                        key={task.id}
                        />
                
                    )}
                </ul>
            </div>
        );
    }
}

export default TodoList;

