import React, { Component } from 'react';
import './css/TaskBox.css';
import './css/DateTimeInput.css'
import asanaIcon from './../../icons/asana.png'
import todoistIcon from './../../icons/todoist.png'
import wunderlistIcon from './../../icons/wunderlist.png'
import githubIcon from './../../icons/github.png'
import gtasksIcon from './../../icons/google-tasks.png'
import trelloIcon from './../../icons/trello.png'
import moment from 'moment'
import DateTimePicker from 'react-date-and-time-picker';
import onClickOutside from "react-onclickoutside";
import db from '../../db';

class TaskBox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: this.props.title,
            date: this.props.date,
            isHiddenDatePicker: true,
            list_id: this.props.list_id
        };
    }
    componentDidMount() {
        var sourceIcon;
        switch (this.props.source) {
            case "wunderlist":
                sourceIcon = wunderlistIcon;
                break;
            case "todoist":
                sourceIcon = todoistIcon;
                break;
            case "googletasks":
                sourceIcon = gtasksIcon;
                break;
            case "asana":
                sourceIcon = asanaIcon;
                break;
            case "trello":
                sourceIcon = trelloIcon
                break;
            case "github":
                sourceIcon = githubIcon
            default:
                break;
        }
        this.setState({
            sourceIcon: sourceIcon
        })

    }
    handleDateClick = () => {
        this.setState({
            isHiddenDatePicker: false
        })
    }
    handleDateChange = date => {
        this.setState({
            date: date
        })
        //Update the database
        db.tasks.update(this.props.id, {date: date.toISOString()})
        /*
        let windowbackground = window.chrome.extension.getBackgroundPage();
        
        switch (this.props.source) {
            case "wunderlist":
                windowbackground.WunderlistAPI.http.tasks.update(this.props.id, this.state.list_id, {
                    "due_date": date.toISOString()
                }).done(res => {
                    this.setState({
                        list_id: res.revision
                    })
                });
                break;
            case "todoist":
                windowbackground.todoistupdate = null;
                windowbackground.todoistupdate = new windowbackground.Todoist();
                windowbackground.todoistupdate.token = localStorage.getItem("tokenTodoist");
                windowbackground.todoistupdate.sync.oncomplete = data => {
                    windowbackground.todoistupdate.updateItem({ "id": this.props.id, "due_date_utc": date.toISOString(), "date_string": date.toISOString() });
                    windowbackground.todoistupdate.write();
                }
                windowbackground.todoistupdate.sync();
                /*
                windowbackground.todoist.updateItem({ "id": this.props.id, "due_date_utc": TodoistDateString(this._d) });
                windowbackground.todoist.write();
                
                break;
            case "googletasks":
                windowbackground.authenticatedXhrCalendar("PUT", `https://www.googleapis.com/tasks/v1/lists/${this.state.list_id}/tasks/${this.props.id}?alt=json`, () => {

                }, JSON.stringify({
                    "due": date.toISOString(),
                    "id": this.props.id
                }));
                break;
            case "asana":
                windowbackground.client.tasks.update(this.props.id, {
                    "due_on": date.toISOString()
                });
                break;
            case "trello":
                windowbackground.Trello.put(`/cards/${this.props.id}`, { due: date.toISOString() });
                break;
            default:
                break;

        }
        */
    }
    handleClickOutside = evt => {
        this.setState({
            isHiddenDatePicker: true
        })
    };
    render() {
        return (
            <li className={this.state.source} id={this.props.id}>
                <input className="taskTitle" spellCheck={false} value={this.state.title} title={this.state.title}></input>
                <span className={moment(new Date()).isBefore(this.state.date) ? "dateSpan" : "dateSpan overdueTask"} onClick={this.handleDateClick}>{moment(this.state.date).fromNow()}</span>
                <img className="taskSourceIcon" src={this.state.sourceIcon} alt={`This task was added from ${this.props.source[0].toUpperCase() + this.props.source.slice(1)}`} title={`This task was added from ${this.props.source[0].toUpperCase() + this.props.source.slice(1)}`} ></img>
                {!this.state.isHiddenDatePicker &&
                    <div className="wrapperDate">
                        <DateTimePicker
                            date={new Date(this.state.date) || new Date()}
                            onChange={this.handleDateChange}
                        />
                    </div>
                }
            </li>
        );
    }
}

export default onClickOutside(TaskBox);

